import { mxClipboard } from "@edp-pmf/mxgraph-ts";
import { IEditorUi } from "../../ui/IEditorUi";
import { KeyCodes } from "../../util/KeyCodes";
import { PmfEvents } from "../../util/PmfEvents";
import { Action } from "../Action";
import { ActionKeys } from "../ActionKeys";

export class CopyAction extends Action {
    constructor(editorUi: IEditorUi) {
        super(ActionKeys.pmf_copy, editorUi, true);

        this.shortcutControlKey = true;
        this.shortcutShiftKey = false;
        this.shortcutKeyCode = KeyCodes.C;

        this.editorUi.addListener(PmfEvents.SelectionModelChanged, () => {
            let selected = !this.editorUi.graph.isSelectionEmpty();
            this.setEnabled(selected);
        });
    }

    execute(evt?: PointerEvent | KeyboardEvent): void {
        let graph = this.editorUi.graph;
        mxClipboard.copy(graph);
    }
}